import { Button, Col, Modal, Row, Space, Table, Tag } from "antd"
import Text from "antd/lib/typography/Text"
import { useEffect, useRef, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { getQuestAsync, getQuestLvAsync, isLoading, questList, questLv } from "./questSlice"
import { VList } from 'virtual-table-ant-design'
import Search from "antd/lib/input/Search"
import { useHotkeys } from 'react-hotkeys-hook'

let lastIndex = 0
const updateIndex = () => {
  lastIndex++
  return lastIndex
}

export default function Quest() {
  const o_quests = useSelector(questList)
  const [quests, setQuests] = useState(o_quests)
  const [visible, setVisible] = useState(false)
  const [quest, setQuest] = useState({})
  const loading = useSelector(isLoading)
  const levels = useSelector(questLv)
  const dispatch = useDispatch()
  const searchInput = useRef()

  const columns = [
    {
      title: '레벨',
      key: `lv${updateIndex()}`,
      dataIndex: 'lv',
      filters: levels.map(v => {
        return {
          text: v,
          value: v
        }
      }),
      width: 120,
      onFilter: (value, record) => record.lv.indexOf(value) === 0,
    },
    {
      title: '퀘스트',
      key: `name${updateIndex()}`,
      render: (_, r) => 
        <Space direction="vertical">
          <div>
            <Tag color="green">{r.type}</Tag>
            { r.key && <Tag color="red">{r.key}</Tag> }
            { r.talk !== 0 && <Tag color="gold">대화</Tag> }
          </div>
          <Text>{r.ko}</Text>
          <Text type="secondary">{r.jp}</Text>
        </Space>,
      filters: [
        {text: '긴급', value: '긴급' },
        {text: '키', value: '키' }
      ],
      onFilter: (value, record) => record.key === value,
      width: 500
    },
    {
      title: '맵',
      key: `map${updateIndex()}`,
      dataIndex: 'map',
      width: 150,
      responsive: ['xxl']
    },
    {
      title: '시간',
      key: `time${updateIndex()}`,
      dataIndex: 'time',
      width: 70,
      responsive: ['xxl']
    },
    {
      title: '계약금',
      key: `contract${updateIndex()}`,
      render: (_,r) => {
        if (r.contract === 0) return '-'
        return r.contract + 'z'
      },
      width: 90,
      responsive: ['xxl']
    },
    {
      title: '메인 타겟',
      key: `goal${updateIndex()}`,
      render: (_, r) => <Space direction="vertical">
        <Text>{r.goal}</Text>
        <Text type="secondary">보수: {r.reward}z / {r.reward / 10}pt / {r.hrp}HRP</Text>
      </Space>,
      width: 500,
      responsive: ['xxl']
    },
    {
      title: '서브 타겟',
      key: `goal2${updateIndex()}`,
      render: (_, r) => <Space direction="vertical">
        <Text>{r.goal2}</Text>
        { r.goal2 !== '없음' && <Text type="secondary">보수: {r.reward2}z / {r.reward2 / 10}pt / {r.hrp2}HRP</Text> }
        
      </Space>,
      width: 500,
      responsive: ['xxl']
    }
  ]

  useEffect(() => {
    dispatch(getQuestLvAsync())
    dispatch(getQuestAsync())
  }, [dispatch])

  useEffect(() => {
    setQuests(o_quests)
  }, [o_quests])

  const onSearch = (value) => {
    setQuests(o_quests.filter(v => v.ko.includes(value)))
  }

  useHotkeys('ctrl+f', (e) => {
    searchInput.current.focus({
      cursor: 'all',
    })
    e.preventDefault();  
  });

  const onRow = (q, i) => {
    return {
      onClick: (e) => {
        setQuest(q)
        setVisible(true)
      }
    }
  }

  const onKeyDownSearch = (e) => {
    if (e.keyCode === 114 || (e.ctrlKey && e.keyCode === 70)) {        
        e.preventDefault();         
        searchInput.current.focus({
          cursor: 'all',
        })   
    }    
  }

  const closeModal = () => {
    setQuest({})
    setVisible(false)
  }

  const makeColumns = (title, k) => {
    const rewardColumns = [
      { 
        title: '아이템', 
        key: `rItem${k}${updateIndex()}`, 
        render: (_, r) => <Space direction="vertical"><Text>{r.ko}</Text><Text type="secondary">{r.jp}</Text></Space>
      },
      {
        title: '수량',
        key:`rQty${k}${updateIndex()}`,
        dataIndex: 'qty',
      },
      {
        title: '확률',
        key: `rPerc${k}${updateIndex()}`,
        render: (_,r) => Math.floor(r.percent * 100) + '%'
      }
    ]

    return [
      {
        title: title,
        key: `rt${k}${updateIndex()}`,
        children: rewardColumns
      }
    ]
  }

  return (
    <>
      <Space style={{ marginBottom: 16 }}>
        <Search placeholder="퀘스트 명 검색" 
          allowClear onSearch={onSearch} style={{ width: 400 }} 
          onKeyDown={onKeyDownSearch}
          ref={searchInput}/>
      </Space>
      <Table columns={columns} dataSource={quests} pagination={false} 
      loading={loading}
      rowKey='id' 
      rowClassName='more_detail'
      onRow={onRow}
      scrollToFirstRowOnChange={true}
      scroll={{ y: '80vh', x: 700 }}
      components={VList({
        height: 1000,
      })}/>
      <Modal width='50vw' visible={visible} title="퀘스트 상세" onCancel={closeModal} footer= {[
        <Button key="close" onClick={closeModal}>
          닫기
        </Button>
      ]}>
        <Row gutter={[16, 32]}>
          <Col span={24}>
            <Space direction="vertical">
              <Space size={20}>
                <Text strong>퀘스트 이름</Text>
                <div>
                  <Tag color="green">{quest.type}</Tag>
                  { quest.key && <Tag color="red">{quest.key}</Tag> }
                  { quest.talk !== 0 && <Tag color="gold">대화</Tag> }
                </div>
              </Space>
              
              <Text>{quest.lv}&nbsp;&nbsp;&nbsp;&nbsp;{quest.ko} / {quest.jp}</Text>
            </Space>
          </Col>
          <Col span={8}> 
            <Space direction="vertical">
              <Text strong>맵</Text>
              <Text>{quest.map}</Text>
            </Space>
          </Col>
          <Col span={8}> 
            <Space direction="vertical">
              <Text strong>시간제한</Text>
              <Text>{quest.time}</Text>
            </Space>
          </Col>
          <Col span={8}> 
            <Space direction="vertical">
              <Text strong>계약금</Text>
              <Text>{quest.contract === 0 ? '-' : quest.contract + 'z'}</Text>
            </Space>
          </Col>
          <Col span={12}>
            <Space direction="vertical">
              <Text strong>메인 타겟</Text>
              <Text>{quest.goal}</Text>
              <Text type="secondary">보수: {quest.reward}z / {quest.reward / 10}pt / {quest.hrp}HRP</Text>
            </Space>
          </Col>
          <Col span={12}>
            <Space direction="vertical" >
              <Text strong>서브 타겟</Text>
              <Text>{quest.goal2}</Text>
              <Text type="secondary">보수: {quest.reward2}z / {quest.reward2 / 10}pt / {quest.hrp2}HRP</Text>
            </Space>
          </Col>
          <Col span={24}>
            <Text strong>퀘스트 보수</Text>
          </Col>
          { quest.rewards && quest.rewards.A && quest.rewards.A.length > 0 && <Col span={quest.rewards_span}><Table size="small" rowKey='key_id' columns={makeColumns('메인 타겟 보수 첫째줄', 'a')} dataSource={quest.rewards.A} pagination={false}/></Col>}
          { quest.rewards && quest.rewards.B && quest.rewards.B.length > 0 && <Col span={quest.rewards_span}><Table size="small" rowKey='key_id' columns={makeColumns('메인 타겟 보수 둘째줄', 'b')} dataSource={quest.rewards.B} pagination={false}/></Col>}
          { quest.rewards && quest.rewards.D && quest.rewards.D.length > 0 && <Col span={quest.rewards_span}><Table size="small" rowKey='key_id' columns={makeColumns('메인 타겟 보수 셋째줄', 'd')} dataSource={quest.rewards.D} pagination={false}/></Col>}
          { quest.rewards && quest.rewards.C && quest.rewards.C.length > 0 && <Col span={quest.rewards_span}><Table size="small" rowKey='key_id' columns={makeColumns('서브 타겟 보수', 'c')} dataSource={quest.rewards.C} pagination={false}/></Col>}
        </Row>
       
      </Modal>
    </>
  )
}